import React, { useEffect, useState } from 'react'
import HomeStyle from './Home.module.css'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'
import gameImg from '../images/counter-illus.png'
import streamimg from '../images/tournament-banner.png'
import Loading from '../Loading/Loading'

export default function Home() {

  const [gamesArray, setGamesArray] = useState([])
  const [isLoad, setIsLoad] = useState(true)

  async function getAllGames() {
    const options = {
      method: 'GET',
      url: 'https://free-to-play-games-database.p.rapidapi.com/api/games',
      headers: {
        'X-RapidAPI-Key': '15d8ebc5d7msh03875dad5a6b0dbp151415jsn9b43122e0296',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      }
    };

    try {
      const response = await axios.request(options);
      // console.log(response.data);
      setGamesArray(response.data)

    } catch (error) {
      console.error(error);
    }
  }
  useEffect(() => {
    getAllGames()

  }, [])

  setTimeout(()=>{
    setIsLoad(false)
  },500)


  return (
    <>
     
       
        {isLoad?<Loading/>:<>
        
        <Helmet>
        <meta charSet="utf-8" />
        <title>Cyborg-Home</title>
      </Helmet>
           {/* welcome home */}
           <div className={HomeStyle.welcomeHome}>
        <div className='container'>
          <div className={`${HomeStyle.welcomeRow} row g-3 align-items-center justify-content-center`}>
            <div className="col-md-12">
              <div className='homeTitle text-center'>
                <p className='text-white fs-3 fw-bold'>Welcome To <span className={`favColor ${HomeStyle.textBorder}`}>Cyborg</span></p>
                <h1 className={HomeStyle.textContainer}>Cyborg</h1>
                <p className='text-white fs-3 fw-bold'><span className={`favColor ${HomeStyle.textBorder}`}>BROWSE</span> OUR POPULAR GAMES NOW</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* end home */}



      <div className={HomeStyle.homePage}>
        <div className={HomeStyle.cupone}></div>
        <div className={HomeStyle.cuptwo}></div>
           
      
      <div className='container mt-3 ' data-aos="fade-left">
        <div className="row g-3 align-items-center">
          <div className="col-md-6">
            <div className={HomeStyle.games}>
              <img src={gameImg} className='w-100' alt="" />
            </div>
          </div>
          <div className={`${HomeStyle.gamesColTwo} col-md-6`}>
            <div className='GamesTitle text-center py-3 '>
              <h3 className='fw-bold'>Most Popular <span className={`favColor animate__animated animate__jackInTheBox  animate__infinite ${HomeStyle.textBorder}`}  >Now</span></h3>
            </div>
            <div className="row g-3 align-items-center ">
              {gamesArray.slice(0, 4).map((game, index) => <div key={index} className='col-md-6'>
                <div className={HomeStyle.gameBox}>
                  <Link to={`/details/${game.id}`}>
                    <div className={HomeStyle.imgLayer}></div>
                    <img src={game.thumbnail} className='w-100 ' alt="" />
                  </Link>
                </div>
              </div>)}
            </div>
          </div>
        </div>
      </div>

      <section className='stream my-5' data-aos="fade-right">
        <div className="container">
          <div className="row g-3 align-items-center">
            <div className="col-md-6">
              <h3 className='fs-1'><i className="fa-solid fa-headset fa-fade me-3" style={{ color: "#e75e8d" }}></i>Live Stream</h3>
              <p>Start your Stream and meet your Fans <span className={`favColor fs-2 fw-bold ${HomeStyle.textBorder}`}>Now</span></p>

            </div>
            <div className="col-md-6">
              <div className={HomeStyle.StreamImg}>
                <img src={streamimg} className='w-100' alt="" />

              </div>
            </div>
          </div>
        </div>
      </section>

      <section className='BrowseGames my-3 ' data-aos="zoom-in-up">
        <div className="container">
          <div className={HomeStyle.BrowsNow}>
            <div className='text-center'>
            <h3 className='mx-4'>Come with me we will play <span className='favColor'>Together</span></h3>
            
            <Link to='/browse'><button className='btn m-auto'>Browse-Now</button></Link>

          
            </div>
              
          </div>
        </div>
      </section>
      </div>

        
        </>}
 

    </>
  )
}
