import React from 'react'
import logo from '../images/logo.png'
export default function Footer() {
  return (
    <>
          <div className='footer text-center p-4' >
            <h6>Copyright © 2023 <span className='favColor'>Cyborg</span> Gaming Company. All rights reserved.</h6>
            <img src={logo} className='footerLogo' alt="Cyborg" />
          </div>
       
    </>
  )
}
