import React from 'react'
import NotFoundStyle from './NotFound.module.css'
import NotFoundImg from '../images/funny-404-error-page-design.gif'
import { Link } from 'react-router-dom'

export default function NotFound()
 {
  return (
    <>
    <div className={NotFoundStyle.home}>
    <div className={NotFoundStyle.moonOne}></div>
        <div className={NotFoundStyle.moontwo}></div>

            <div className='notfound'>
                <img src={NotFoundImg} className='w-75 ' alt="" />
                    <h2 className='favColor fw-bold mb-4'>Error 404</h2>
                <Link to=''>
                <button className={NotFoundStyle.btn}  target='_blank' type="button">
                <strong>Back Home</strong>
                <div className={NotFoundStyle.containerstars}>
                  <div className={NotFoundStyle.stars}></div>
                </div>
                <div className={NotFoundStyle.glow}>
                  <div className={NotFoundStyle.circle}></div>
                  <div className={NotFoundStyle.circle}></div>
                </div>
              </button>
                </Link>
                
            </div>


    </div>
    
    
    
    </>
  )
}
