import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { useParams } from 'react-router-dom'
import DetailsStyle from './Details.module.css'
import imgTitle from '../images/game-banner-illus-4.png'
import Loading from '../Loading/Loading';

export default function Details() {
  let { id } = useParams()
  const [gameDetails, setGameDetails] = useState({})
  const [isLoad, setIsLoad] = useState(true)

  async function getGameDetails() {
    const options = {
      method: 'GET',
      url: 'https://free-to-play-games-database.p.rapidapi.com/api/game',
      params: { id: `${id}` },
      headers: {
        'X-RapidAPI-Key': '15d8ebc5d7msh03875dad5a6b0dbp151415jsn9b43122e0296',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      }
    };

    try {
      const response = await axios.request(options);

      setGameDetails(response.data)
      console.log(response.data);

    } catch (error) {
      console.error(error);
    }

  }
  useEffect(() => {
    getGameDetails()

  }, [])

  setTimeout(()=>{
    setIsLoad(false)
  },1500)

  return (

    <>
    {isLoad?<Loading/>: <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Cyborg-{gameDetails.title}</title>
      </Helmet>

      <div className={DetailsStyle.GameDetails}>
        <div>
          <h2>Games Details</h2>
          <p>{gameDetails.title}</p>
        </div>
        <div>
          <img src={imgTitle} className={DetailsStyle.gameimg} alt="" />
        </div>
      </div>


      <div className={DetailsStyle.mainDiv}>
        <div className={DetailsStyle.moonOne}></div>
        <div className={DetailsStyle.moontwo}></div>
        <div className="container">
        <section className={DetailsStyle.detailsSec}>

          <div className="row g-3 align-items-center">
            <div className={gameDetails.minimum_system_requirements ? 'col-md-4 col-12':'col-md-12'}>
              <div className={DetailsStyle.mainImg}>
                <img src={gameDetails.thumbnail} alt="" />
                <div className={DetailsStyle.mainImgLayer}></div>
              </div>
            </div>
            {gameDetails.minimum_system_requirements ? <>
              <div className="col-md-8 text-start" data-aos="fade-left">
                <div className='my-3'>
                  <p className={DetailsStyle.PHover}><span className='favColor'>Graphics : </span>{gameDetails.minimum_system_requirements.graphics == null ? 'No informations to display...' : gameDetails.minimum_system_requirements.graphics}</p>
                </div>
                <div className='my-3'>
                  <p className={DetailsStyle.PHover}><span className='favColor'>Processor : </span>{gameDetails.minimum_system_requirements.processor == null ? 'No informations to display...' : gameDetails.minimum_system_requirements.processor}</p>
                </div>
                <div className='my-3'>
                  <p className={DetailsStyle.PHover}><span className='favColor'>Storage : </span>{gameDetails.minimum_system_requirements.storage == null ? 'No informations to display...' : gameDetails.minimum_system_requirements.storage}</p>
                </div>
                <div className='my-3'>
                  <p className={DetailsStyle.PHover}><span className='favColor'>Memory : </span>{gameDetails.minimum_system_requirements.memory == null ? 'No informations to display...' : gameDetails.minimum_system_requirements.memory}</p>
                </div>

              </div>

            </> : ''}
          </div>



          <div className="row g-2">
            <div className='col-md-12 mt-4 mb-3'>
              <div> <h3><span className='favColor'>Images</span> from Game</h3> </div>
            </div>
            {gameDetails.screenshots ? <>
              {gameDetails.screenshots.splice(0, 2).map((screen, index) => <div key={index} className='col-md-6 col-6'>
                <img src={screen.image} className='w-100 rounded-3 mainImg borderImg' data-aos="fade-down" alt="" />
              </div>)}

            </> : ''}
            <div className="col-md-12">
              <div className='my-4'> <h3><span className='favColor'>Description</span> About The Game</h3> </div>
              <div className='description text-center' data-aos="fade-up"><p>{gameDetails.description}</p></div>
            </div>


            <div className="col-md-12">
              <a className={DetailsStyle.btn} href={gameDetails.freetogame_profile_url} target='_blank' type="button">
                <strong>play Now</strong>
                <div className={DetailsStyle.containerstars}>
                  <div className={DetailsStyle.stars}></div>
                </div>

                <div className={DetailsStyle.glow}>
                  <div className={DetailsStyle.circle}></div>
                  <div className={DetailsStyle.circle}></div>
                </div>
              </a>

            </div>
          </div>





        </section>


      </div>

      </div>
    





    </>
}
    
    </>
   
  )
}
