import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import NavStyle from './Navbar.module.css'

export default function Navbar() {
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-body-tertiary ">
        <div className="container">
          <a className="navbar-brand">
          <h3 className={NavStyle.textContainer}>Cyborg</h3>
          </a>
          <a className="navbar-toggler mx-3" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon" />
          </a>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav align-items-center  ms-auto mb-2 mb-lg-0">
              
              <li className="nav-item">
                
                <NavLink className="nav-link text-white " to="">Home</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link text-white" to='browse'>Browse</NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link text-white" to='streams'>Stream</NavLink>
              </li>
            
              
            </ul>
            <ul className="navbar-nav ul-brands align-items-center  ms-auto mb-2 mb-lg-0">
              
              <li className="nav-item">
                <a href="https://www.facebook.com/mohamed.o.menisy" target='_blank'><i className='fa-brands fa-facebook '></i></a>
              </li>
              <li className="nav-item">
                <a href="https://twitter.com/Menisy9" target='_blank'><i className='fa-brands fa-twitter '></i></a>
              </li>
              <li className="nav-item">
                <a href="https://www.instagram.com/menisy.4/" target='_blank'><i className='fa-brands fa-instagram  '></i></a>
              </li>
              <li className="nav-item linked">
                <a href="https://www.linkedin.com/in/mohamed-osama-menisy-945400246/" target='_blank'><i className='fa-brands fa-linkedin'></i></a>
              </li>
              
            </ul>

         
            

          </div>
        </div>
      </nav>




    </>
  )
}
