import React, { useEffect, useLayoutEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'
import BrowseStyle from './Browse.module.css'
import cupImg from '../images/banner-play-go-illus.png'
import Slider from 'react-slick'
import Loading from '../Loading/Loading'

export default function Browse() {

  const [browseArray, setBrowseArray] = useState([])
  const [allArrayData, setAllArrayData] = useState([])
  const [inputValue, setInputValue] = useState('')
  const [isLoad, setIsLoad] = useState(true)

  let [more, setMore] = useState(0)
  async function getData() {
    const options = {
      method: 'GET',
      url: 'https://free-to-play-games-database.p.rapidapi.com/api/games',

      headers: {
        'X-RapidAPI-Key': '15d8ebc5d7msh03875dad5a6b0dbp151415jsn9b43122e0296',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      }
    };

    try {
      const response = await axios.request(options);
      setBrowseArray(response.data);
      setAllArrayData(response.data)

    } catch (error) {
      console.error(error);
    }
  }

  useEffect(() => {
    getData()
  }, [])


  useLayoutEffect(() => {
    getData()
  }, [more])


  function Moregames() {
    more += 9
    setMore(more)

  }

  useEffect(() => {
    if (inputValue == "") {
      getData()
    }
  }, [allArrayData])


  function serch(e) {
    setInputValue(e.target.value)
    let myNewArray = allArrayData.filter((oneGame) => oneGame.title.toLowerCase().includes(inputValue.toLowerCase()))
    setAllArrayData(myNewArray)

  }

  setTimeout(()=>{
    setIsLoad(false)
  },1000)


  var settings = {

    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    pauseOnHover: true,
    arrows: false,
    centerMode: true,
    responsive: [

      {
        breakpoint: 767,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
          centerMode: false,
        }
      }
    ]


  };

  return (
   <>
   {isLoad?<Loading/>: <>

<Helmet>
  <meta charSet="utf-8" />
  <title>Cyborg-Browse</title>
</Helmet>

{/* welcome Page */}
<section className={BrowseStyle.browseWelcome}>
  <div>
    <h2 className='fa-3x'>Play As You Go</h2>
  </div>
  <div>
    <img src={cupImg} className='w-100 h-100' alt="" />
  </div>
</section>
{/* end welcome */}

{/* start Slider */}
<div className="container-fluid px-5 my-5 text-center">

  <Slider {...settings}>
    {browseArray.slice(20, 35).map((game, index) => <div key={index} >
      <div className={`mx-2 ${BrowseStyle.sliderDiv}`}>
        <Link to={`/details/${game.id}`}>
          <img src={game.thumbnail} className={`${BrowseStyle.sliderImg}  w-100`} alt="" />
        </Link>
        <div className={BrowseStyle.layer}><h6 className='p-3'>{game.title}</h6></div>
      </div>
    </div>)}

  </Slider>


</div>
{/* end Slider */}


{/* start More Game */}

<div className="container">
  <section className={BrowseStyle.AllGames}>
    <div className="bg"></div>
    <div className="bg bg2"></div>
    <div className="bg bg3"></div>
    
    <div className="row g-4 mb-3 ">
      <div className={BrowseStyle.searchInput}>
        <input type="search" onKeyUp={serch} className='form-control' placeholder='Search' />
      </div>
      {allArrayData.slice(0, 9 + more).map((game, index) => <div key={index} className='col-md-4 ' data-aos="fade-up"
        data-aos-offset="100"
        data-aos-easing="ease-in-sine">
        <Link to={`/details/${game.id}`}>
          <article className={BrowseStyle.card}>
            <div className={BrowseStyle.temporary_text}>
              <img src={game.thumbnail} className='w-100 shadow' alt="" />
            </div>
            <div className={BrowseStyle.card_content}>
              <span className={BrowseStyle.card_title}><span className='favColor'>{game.title}</span></span>
              <span className={`${BrowseStyle.card_subtitle} favColor2 fw-bold`}>{game.platform}</span>


            </div>
          </article>



        </Link>
      </div>)}
      {inputValue ? "" : <div className="col-md-12">
        <div className='MoreGame text-center'>
          <button className={BrowseStyle.moreButton} onClick={Moregames}>More-Games</button>
        </div>
      </div>}
    </div>
  </section>
</div>
{/*end More Game */}
</>}
   
   </>
  )
}
